.NavigationItem {
    margin:10px 0px;
    box-sizing: border-box;
    display: fleblock;
    height: 100%;
    

}

.NavigationItem a {
    color:#8f5c2c;
    text-decoration:none;
    width:100%;
    box-sizing: border-box;
    display: block;

}

.NavigationItem a:hover, .NavigationItem a:active, .NavigationItem a:active {
    color:#40A4c8
}

@media (min-width:500px){
    .NavigationItem {
        margin:0;
        display: flex;
        height: 100%;
        width:auto;
        align-items: center;
    
    }
    
    .NavigationItem a {
        color:white;
        text-decoration:none;
        height:100%;
        padding: 16px 10px;
        border-bottom: 4px solid transparent;
        display: block;
        margin:2px;
    
    }
    
    .NavigationItem a:hover, .NavigationItem a:active, .NavigationItem a.active {
        background-color: #8F5C2C;
        border-bottom:4px solid #40A4C8;
        color:white;
    }

}