.SideDrawer{
    position: fixed;
    width:280px;
    height:100%;
    max-width: 70%;
    left:0;
    top:0;
    z-index: 200;
    background-color: white;
    padding:32px 16px;
    box-sizing: border-box;
    transition: transform 0.3s ease-out;
}

@media (min-width: 500px) {
    .SideDrawer{
        display:none;
    }
}

.Open{
    transform: translate(0%);
}

.Close {
    transform: translate(-100%)
}

.Logo{ 
    height: 11%;
}