.Toolbar{
    height:56px;
    width:100%;
    position:fixed;
    top:0;
    left:0;
    background-color: #703b09;
    display:flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 10px;
    box-sizing: border-box;
    z-index:90;
}
.Toolbar nav {
    height:100%;
}
.Menu {
 color:#fff;
 cursor:pointer
}

.Logo {
    height: 80%;
}
@media (min-width:1025px) { 
    .Menu{
        display:none;
    }
 }

@media (max-width: 499px) {
    .DesktopOnly{
        display:none;
    }
}